import React from 'react';

import styles from './clubItem.module.css';

const clubItem = (props) => {
    return (
        <div 
            className={`${styles.ClubItem} shadow`}
            onClick={props.click}
        >
            <i></i>
            <div className={styles.wrapper}>
                <h4>{props.clubName}</h4>
                <p>{props.clubDesc}</p>
                <p className={styles.clubType}>{props.clubType}</p>
            </div>
        </div>
    );
};



export default clubItem;